.container {
    cursor: url('https://uploads.codesandbox.io/uploads/user/b3e56831-8b98-4fee-b941-0e27f39883ab/Ad1_-cursor.png') 39 39,
      auto;
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
  }
  
  .App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-size: cover; /* Cover the entire container */
    background-position: top center; /* Center the image */
    padding: 20px;
  }
  
  .deck {
    position: absolute;
    width: 300px;
    will-change: transform;
    container-name: deck;
    display: flex;
    align-items: stretch;
    justify-content: center;
    touch-action: none;
  }

  .deckTeste {
    position: unset;
    width: 18%;
    display: flex;
    align-items: stretch;
    justify-content: center;
    touch-action: none;
  }
  
  .deck > .marijoke, .deckTeste > .marijoke {
    text-shadow: #ffffffd2 1px 0 10px;
    background-color: #df943d;
  }
  
  .deck > .cantada, .deckTeste > .cantada {
    text-shadow: #dd4cadd2 1px 0 10px;
    background-color: transparent;
    background: radial-gradient(ellipse farthest-corner at left bottom, #ff58c7 2%, #dd4cad 8%, #c44399 30%, #ad3786 50%, transparent 80%),
    radial-gradient(ellipse farthest-corner at right top, #FFFFFF 1%, #fc83d3 1%, #d69bc2 28%, #a73982 62.5%, #a73982 80%);
  }
  
  .deck > .expecial, .deckTeste > .expecial {
    text-shadow: #fdb931 1px 0 10px;
    background-color: transparent;
    background: radial-gradient(ellipse farthest-corner at left bottom, #FEDB37 2%, #FDB931 8%, #9f7928 30%, #8A6E2F 50%, transparent 80%),
    radial-gradient(ellipse farthest-corner at right top, #FFFFFF 1%, #FFFFAC 1%, #D1B464 28%, #5d4a1f 62.5%, #5d4a1f 80%);
  }
  
  .deck > .charajoes, .deckTeste > .charajoes {
    text-shadow: #3f31fd 1px 0 10px;
    background-color: transparent;
    background: radial-gradient(ellipse farthest-corner at left bottom, #3a37fe 2%, #3431fd 8%, #282a9f 30%, #2f318a 50%, transparent 80%),
    radial-gradient(ellipse farthest-corner at right top, #ffffff 1%, #ace8ff 1%, #64b8d1 20%, #1f5b5d 50%, #1f335d 80%);
  }
  
  .deck > div, .deckTeste > div {
    text-shadow: #242424d2 1px 0 10px;
    background-color: #df3f3d;
    background-size: auto 85%;
    background-repeat: no-repeat;
    background-position: center center;
    width: 50vh;
    height: 60vh;
    max-height: 1000px;
    will-change: transform;
    border-radius: 10px;
    box-shadow: 0 12.5px 30px 0px rgba(50, 50, 73, 0.01), 0 10px 20px -10px rgba(50, 50, 73, 0.07);
  }
  
  .programa {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 5px;
    color: transparent;
    text-shadow: 4px 4px 7px rgb(223, 63, 61, 0.6), 0px 0px 0px #1e1e1e82;
  }
  
  .marijoke > div > .programa {
    text-shadow: 4px 4px 7px rgba(223, 148, 61, 0.6), 0px 0px 0px #1e1e1e82;
  }
  
  .cantada > div > .programa {
    text-shadow: 4px 4px 7px rgb(221, 76, 173, 0.6), 0px 0px 0px #1e1e1e82;
  }
  
  .expecial > div > .programa {
    text-shadow: 4px 4px 7px rgb(116, 92, 39, 0.6), 0px 0px 0px #1e1e1e82;
  }
  
  .charajoes > div > .programa {
    text-shadow: 4px 4px 7px rgba(44, 52, 131, 0.6), 0px 0px 0px #1e1e1e82;
  }
  
  .marijoke {
    background-color: #df943d;
  }
  
  .cantada {
    background-color: #dd4cad;
    background: radial-gradient(ellipse farthest-corner at left bottom, #ff58c7 2%, #dd4cad 8%, #c44399 30%, #ad3786 50%, transparent 80%),
    radial-gradient(ellipse farthest-corner at right top, #FFFFFF 1%, #fc83d3 1%, #d69bc2 28%, #a73982 62.5%, #a73982 80%);
  }
  
  .expecial {
    background-color: transparent;
    background: radial-gradient(ellipse farthest-corner at left bottom, #FEDB37 2%, #FDB931 8%, #9f7928 30%, #8A6E2F 50%, transparent 80%),
    radial-gradient(ellipse farthest-corner at right top, #FFFFFF 1%, #FFFFAC 1%, #D1B464 28%, #5d4a1f 62.5%, #5d4a1f 80%);
  }
  
  .charajoes {
    background-color: transparent;
    background: radial-gradient(ellipse farthest-corner at left bottom, #3a37fe 2%, #3431fd 8%, #282a9f 30%, #2f318a 50%, transparent 80%),
    radial-gradient(ellipse farthest-corner at right top, #ffffff 1%, #ace8ff 1%, #64b8d1 20%, #1f5b5d 50%, #1f335d 80%);
  }
  
  .padrao {
    background-color: #df3f3d;
  }
  
  @media only screen and (max-width: 600px) {
    .deck > div {
      height: 80vh;
      margin-top: 10%;
    }
    button > .default {
      width: 40px;
      height: 40px;
    }
  }
  
  @media only screen and (min-width: 601px) and (max-width: 800px) {
    .deck > div {
      height: 70vh;
    }
  }